.menu {

  @media #{$breakpointTablet} {
    position: relative;
  }

  @media #{$breakpointMobile} {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    left: -20px;
    top: 100%;
    right: -20px;
    padding: 0 20px 32px;
    box-shadow: 4px 4px 10px #F0F0F0;
    background-color: rgba($gray-medium, 1);
    transform: translateX(-1000px);
    transition: all 0.3s linear;
    z-index: 7;
  }

  &--open {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
    height: 100vh;
  }

  &:before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: rgba($gray-light, 0.5);
    height: 0;
    z-index: 7;
    opacity: 0;
    transition: all 0.1s linear;
  }

  &--open:before {
    height: 100vh;
    opacity: 1;
  }
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0;
  padding: 0;

  @media #{$breakpointTablet} {
    justify-content: space-between;
    margin: 0 -8px;
  }

  @media #{$breakpointMobile} {
    flex-direction: column;
    justify-content: center;
  }
}

.menu__item {
  position: relative;
  list-style: none;
  padding: 24px 8px;
  transition: all 0.2s ease-in-out;

  @media #{$breakpointMobile} {
    width: 100%;
    padding: 16px 8px;
  }

  .header--scrolled & {
    padding: 12px 8px;
  }

  &--dd {
    background-color: rgba($gray-medium, 1);
    margin: 0;
    padding: 0 8px 8px 8px;

    @media #{$breakpointMobile} {
      padding: 8px;
    }

    .header--homepage & {
      background-color: rgba($gray-medium, 0.5);
    }

    .header--scrolled & {
      background-color: rgba($gray-medium, 1);
    }
  }
}

.menu__link {
  outline: none;
  display: inline-block;
  color: currentColor;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  padding-top: 4px;
  background-color: transparent;
  border: none;
  @include underline;

  @media #{$breakpointMobile} {
    font-size: 20px;
  }

  &--accent {
    color: $accent-dark;
    transition: all 0.15s linear;

    &:hover,
    &:focus {
      text-decoration: none;
      color: $accent-red;
    }
  }

  &--xl {
    padding: 8px 12px;
    font-size: 20px;
  }
}

.menu__dd {
  position: absolute;
  top: 100%;
  left: 0;
  margin: 0;
  list-style: none;
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  transition: all 0.25s ease-in-out;
  box-shadow: 2px 2px 2px rgba($gray-dark, 0.5);
  padding: 0;

  .menu__item:hover & {
    visibility: visible;
    opacity: 1;
  }

  @media #{$breakpointMobile} {
    position: static;
    visibility: visible;
    opacity: 1;
    padding: 16px 8px 0;
    box-shadow: none;
  }
}
