@font-face {
  font-family: 'Poiret One';
  font-weight: 400;
  font-style: normal;
  src: local('Poiret One'), url('/fonts/poiretone-regular-webfont.woff2') format('woff2'), url('/fonts/poiretone-regular-webfont.woff') format('woff');
}


body {
  margin: 0;
  color: $black;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  font-family: $main-font;

  &.open {
    @media #{$breakpointMobile} {
      overflow: hidden;
    }
  }

  * {
    box-sizing: border-box;

    &::selection {
      background: $secondary;
      /* Safari */
    }

    &::-moz-selection {
      background: $secondary;
      /* Firefox */
    }
  }
}

main {
  flex-grow: 1;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  -webkit-clip-path: inset(100%)
}

.desktop-hidden {
  display: none;

  @media #{$breakpointTablet} {
    display: block;
  }
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;

  @media #{$breakpointMobile} {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.content {
  display: flex;
}

.content__block {
  &--half {
    width: 50%;
    padding: 32px;
    background-color: rgba($gray-medium, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  p {
    font-size: 20px;
    line-height: 1.5;
    margin: 0;
    padding: 0 0 16px;
  }
}

h1,
h2,
h3,
h4,
h5 {
  // font-family: $title-font;
  font-weight: 500;
  margin-top: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

p {
  font-size: 16px;
  margin: 0;
  padding: 8px 0;
}

a {
  font-size: 16px;
  margin: 0;
  padding: 8px 0;
  text-decoration: none;
  color: inherit;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}
