.header {
  transition: all 0.2s ease-in-out;
  background-color: rgba($gray-medium, 1);
  margin-bottom: 16px;

  &--homepage {
    background-color: rgba($gray-medium, 0.5);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
    margin-bottom: 0;
  }

  &--scrolled {
    background-color: rgba($gray-medium, 1);
    box-shadow: 0px 4px 5px 0px rgba($black, 0.15);
  }

  &--open {
    @media #{$breakpointMobile} {
      background-color: rgba($gray-medium, 1);
    }
  }
}

.header__inner {

  @media #{$breakpointTablet} {
    position: relative;
  }

  @media #{$breakpointMobile} {}
}

.header__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;

  @media #{$breakpointTablet} {
    flex-wrap: wrap;
    padding-top: 16px;
  }

  @media #{$breakpointMobile} {
    margin-left: 0;
    margin-right: auto;
    position: static;
    width: 100%;
    padding-bottom: 16px;
  }
}

.header__contacts {
  order: 1;

  @media #{$breakpointTablet} {
    order: 0;
  }

  @media #{$breakpointMobile} {
    margin-left: 0;
    order: -1;
  }

  @media #{$breakpointMobileXS} {
    padding-top: 8px;
    width: 100%;
    order: 1;
  }
}


.header__menu {
  margin-left: auto;
  margin-right: auto;

  @media #{$breakpointTablet} {
    order: 1;
    width: 100%;
  }

  @media #{$breakpointMobile} {}
}

.header__logo {
  font-size: 16px;
  padding: 4px 0 0;
  flex-shrink: 0;
  font-weight: 500;
  @include underline;

  &:hover,
  &:focus {
    outline: none;
    text-decoration: none;
  }

  @media #{$breakpointMobile} {
    margin-left: auto;
    margin-right: auto;
  }

  @media #{$breakpointMobileXS} {
    margin-left: 0;
  }
}

.header__btn {
  display: none;

  @media #{$breakpointMobile} {
    display: block;
  }
}
