.quiz {
  background-color: $white;
}

.quiz__questions {
  padding: 16px 0;
  margin: 0;
  list-style: none;
}

.quiz__question {
  h3 {
    padding-bottom: 16px;
  }
}

.quiz__answer {
  display: flex;
  align-items: center;
  transition: all 0.25s ease-in;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.5;
  }
}

.quiz__answer input[type=radio]+span {
  position: relative;
  font-size: 16px;
  padding: 8px 32px;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    width: 20px;
    height: 20px;
    border: 1px solid $black;
    box-sizing: border-box;
    background-color: transparent;
    transform: translateY(-50%);
    border-radius: 50%;
  }

  &::after {
    content: '';
    position: absolute;
    left: 7px;
    top: 50%;
    width: 6px;
    height: 6px;
    background-color: transparent;
    transform: translateY(-50%);
    border-radius: 50%;
  }
}

.quiz__answer input[type=radio]:checked+span::after {
  background-color: $black;
}

.quiz__header {
  padding-top: 16px;
}

.quiz__footer {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  margin-right: -16px;
  padding: 16px 0 0;

  @media #{$breakpointMobile} {
    flex-wrap: wrap;
    justify-content: center;
  }

  .btn {
    margin: 0 16px;

    @media #{$breakpointMobile} {
      margin: 0 16px 16px;
    }
  }
}

.quiz__result {
  p {
    font-size: 20px;
  }

  p:first-child {
    padding-bottom: 16px;
  }
}

.quiz__cta {
  margin: 24px 0;

  p {
    font-size: 24px;
    max-width: 70%;
    margin: 0 auto;
    text-align: center;
    font-weight: 700;
  }

  p:last-of-type {
    padding-bottom: 24px;
  }
}
