.price {
  margin-bottom: 32px;
}

.price__list {
  margin: 0 0 24px;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  // @media #{$breakpointTablet} {
  //   width: 75%;
  //   margin: 0 auto;
  // }

  // @media #{$breakpointMobile} {
  //   flex-direction: column;
  // }
}

.price__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 280px;
  padding-bottom: 24px;

  h3 {
    padding-top: 16px;
    padding-bottom: 8px;
    margin: 0;
  }

  p {
    font-weight: 700;
  }
}

.price__img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
}

.price__stages {
  margin: 0 0 24px;
  padding: 0 0 0 20px;
  font-size: 16px;
  line-height: 1.5;

  li {
    padding-bottom: 8px;
  }

  h3 {
    margin: 0;
    font-weight: 700;
  }

  p {
    padding: 0;
  }

}

ul.price__stages {
  list-style: circle;
}
