.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.glide * {
  box-sizing: inherit;
}

.glide__track {
  overflow: hidden;
}

.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform;
}

.glide__slides--dragging {
  user-select: none;
}

.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: normal;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide--rtl {
  direction: rtl;
}

// arrows

.glide__arrow {
  display: flex;
  position: absolute;
  top: 50%;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  outline: 0;
  transform: translateY(-50%);
  border-radius: 50%;
  transition: all .25s ease-in-out;
  cursor: pointer;
  background: transparent;
  border: 0;
  color: $primary;

  &--left {
    padding-right: 4px;
    left: -40px;

    @media #{$breakpointMobile} {
      left: -20px;
    }
  }

  &--right {
    padding-left: 4px;
    right: -40px;

    @media #{$breakpointMobile} {
      right: -20px;
    }
  }

  svg {
    margin: auto;
    fill: currentColor;
    transition: all .25s ease-in-out;
  }

  &:hover svg,
  &:focus svg {
    transform: scale(1.15);
  }
}


// for fadeout effect!!!
.glide--intro {

  .glide__slides {
    transform: translate3d(0, 0, 0) !important;
  }

  .glide__slide {
    position: absolute;
    top: 0;
    left: 0;
    order: 2;
    opacity: 0;
    transition: opacity 1250ms ease-in-out;
  }

  .glide__slide.glide__slide--active {
    position: relative;
    order: 1;
    opacity: 1;
    z-index: 1;
  }
}
