.about {
  margin-bottom: 16px;
  background-color: $gray-medium;
  padding-bottom: 16px;
}

.about__article {

  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.about__header {
  position: relative;
  overflow: hidden;
  margin-bottom: 16px;
}

.about__blurred {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  filter: blur(10px);
  width: 100%;
  height: auto;

  @media #{$breakpointMobile} {
    display: none;
  }
}

.about__content {

  img {
    width: 50%;
    height: auto;
    object-fit: cover;
    object-position: center;
    float: right;
    margin-left: 32px;

    @media #{$breakpointMobile} {
      margin: 0;
      width: 100%;
      max-width: 350px;
    }

    @media #{$breakpointMobileXS} {
      max-width: 100%;
    }
  }

  &--row {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;

    @media #{$breakpointMobile} {
      flex-direction: column;
    }
  }
}

.about__text {

  p {
    font-size: 20px;
    line-height: 1.5;
    padding: 16px 0;
    text-align: center;
  }

  h3,
  h4,
  h5 {
    font-size: 24px;
    line-height: 1.5;
    padding: 16px 0;
    margin: 0;
    text-align: center;
  }
}

.about__quote {
  font-size: 32px;
  line-height: 1.5;
  text-align: center;
  padding: 16px 0;
  margin: 0;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  text-shadow: 1px 1px 10px $gray-medium;

  @media #{$breakpointMobileXS} {
    font-size: 24px;
  }
}
