.project {
  margin-bottom: 32px;
}

.project__block {
  width: 100%;
  display: flex;
  align-items: center;

  @media #{$breakpointMobile} {
    flex-direction: column-reverse;
  }

  img {
    object-position: left;
    object-fit: contain;
    margin: auto;
    max-height: 500px;

    @media #{$breakpointMobile} {
      padding-right: 0;
    }
  }

  &:nth-child(2n-1) img {
    padding-right: 16px;

    @media #{$breakpointMobile} {
      padding-right: 0;
      object-position: center;
    }
  }

  &:nth-child(2n) {
    flex-direction: row-reverse;
    text-align: left;

    @media #{$breakpointMobile} {
      flex-direction: column-reverse;
      text-align: right;
    }
  }

  &:nth-child(2n) img {
    object-position: right;
    padding-left: 16px;

    @media #{$breakpointMobile} {
      padding-left: 0;
      object-position: center;
    }
  }
}

.project__img {
  height: 100%;
  width: 50%;
  flex-shrink: 0;
  display: flex;

  @media #{$breakpointMobile} {
    width: 80%;
  }
}

.project__text {
  padding: 16px;
  width: 100%;
  text-align: justify;

  @media #{$breakpointMobile} {
    padding: 16px 0;
  }

  p {
    font-size: 20px;
    line-height: 2;

    @media #{$breakpointMobile} {
      line-height: 1.5;
    }
  }

  p.highlighted {
    margin: 0 auto;
    padding: 8px;
    width: 80%;
    border: 1px solid $primary;
    text-align: center;
  }

  h2,
  h3,
  h4 {
    line-height: 2;
    padding: 0 0 8px;
  }

  table {
    margin: 0 auto;
  }
}
