.press__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 0 16px;
  margin: 0;
}

.press__item {
  width: 300px;
  margin: 16px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media #{$breakpointTablet} {
    margin: 16px;
  }

  @media #{$breakpointMobileXS} {
    width: 100%;
    margin: 16px 0;
  }
}

.press__flat {
  outline: none;
  border: 1px solid $gray-dark;
  position: relative;
  width: 300px;
  margin: auto;
  overflow: hidden;

  @media #{$breakpointMobileXS} {
    width: 100%;
  }


  img {
    width: 100%;
    height: auto;
    object-fit: contain;
    object-position: center;
  }
}

.press__source {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
}
