.form__field {
  border: none;
  padding: 0;
  margin: 0;
  padding-bottom: 16px;
}

.form__label {
  display: block;
  position: relative;
  margin: auto;
  width: 100%;
  max-width: 400px;
  border-radius: 3px;
  overflow: hidden;
}

.form__input-label {
  position: absolute;
  top: 20px;
  left: 12px;
  font-size: 16px;
  color: rgba($black, 0.5);
  font-weight: 500;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all .2s ease;
  pointer-events: none;
}

.form__focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba($gray-dark, 0.05);
  z-index: -1;
  transform: scaleX(0);
  transform-origin: left;
}

.form__input {
  appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  padding: 16px 12px 0 12px;
  height: 56px;
  font-size: 16px;
  font-weight: 400;
  background: rgba($black, 0.02);
  box-shadow: inset 0 -1px 0 rgba($black, 0.3);
  color: $black;
  transition: all .15s ease;


  &:hover {
    background: rgba($black, 0.04);
    box-shadow: inset 0 -1px 0 rgba($black, 0.5);
  }

  &:not(:placeholder-shown)+.form__input-label {
    color: rgba($black, 0.5);
    transform: translate3d(0, -12px, 0) scale(0.75);
  }

  &:focus {
    background: rgba($black, 0.05);
    outline: none;
    box-shadow: inset 0 -2px 0 $secondary;
  }

  &:focus+.form__input-label {
    color: $black;
    transform: translate3d(0, -12px, 0) scale(0.75);
  }

  &:focus+.form__input-label+.form__focus-bg {
    transform: scaleX(1);
    transition: all .1s ease;
  }

  &:invalid:not(:placeholder-shown) {
    box-shadow: inset 0 -1px 0 $accent-red;
  }
}
