@mixin underline {
  position: relative;
  padding-bottom: 4px;
  // margin-bottom: 4px;
  // margin-left: 4px;
  // margin-right: 4px;
  transition: all 0.20s ease-in-out;

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 100%;
    height: 1px;
    background-color: currentColor;
    transition: all 0.20s linear;
  }

  &:hover,
  &:focus {
    text-decoration: none;

    &:before {
      left: 0;
      right: 0%;
    }
  }
}
