.filter__list {
  margin: 0;
  padding: 0 0 16px;
  list-style: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.filter__item {
  min-width: 80px;
  text-align: center;
  padding: 8px 8px 6px;
  margin-right: 16px;
  border-radius: 20px;
  border: 1px solid $primary;
  color: $primary;
  text-transform: uppercase;
  background-color: rgba($secondary, 0.5);
  cursor: pointer;
  transition: all 0.25s ease-in;
  font-size: 20px;

  &:hover,
  &:focus {
    outline: none;
    background-color: rgba($secondary, 1);
  }

  &--active {
    background-color: rgba($accent-yellow, 1);

    &:hover,
    &:focus {
      background-color: rgba($accent-yellow, 1);
    }
  }
}

.filter__product {
  transition: all 0.25s ease-in;

  &--hidden {
    display: none;
  }
}
