.feedback {
  background-color: $gray-medium;
  margin-bottom: 16px;
}

.feedback__inner {
  padding-top: 16px;
  padding-bottom: 32px;
}

.feedback__list {
  margin: 0;
}

.feedback__item {
  display: flex;
  background-color: $white;
  padding: 16px;
  height: auto;

  @media #{$breakpointTablet} {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
  }

  @media #{$breakpointMobile} {
    background-color: $gray-medium;
  }

}

.feedback__user {
  width: 80px;
  height: 80px;
  background-color: $gray-medium;
  flex-shrink: 0;
  margin-right: 16px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid $gray-dark;

  @media #{$breakpointTablet} {
    margin-right: 0;
    margin-bottom: 16px;
  }


  img {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
  }
}

.feedback__text {
  display: flex;
  flex-direction: column;

  em,
  q {
    font-size: 16px;
    line-height: 1.5;
  }
}
